(function(){
  'use strict';

    apiService.$inject = ["$http", "APIURL"];
  angular
    .module('app')
    .factory('apiService', apiService);

    /*@ngInject*/
    function apiService($http, APIURL){
      return {
        getMovies: getMovies,
        getSeries: getSeries,
        getFoods: getFoods
      };

      function getMovies(){
        return $http.get(APIURL + 'fakeApi/movies.json');
      }

      function getSeries(){
        return $http.get(APIURL + 'fakeApi/series-by-season.json');
      }

      function getFoods(){
        return $http.get(APIURL + 'fakeApi/foods.json');
      }
    }
})();

(function(){
  'use strict';

    answerService.$inject = ["$http", "APIURL"];
  angular
    .module('app')
    .factory('answerService', answerService);

    /*@ngInject*/
    function answerService($http, APIURL){
      var movieId = '';
      var movies = '';
      var foodId = '';
      var foods = '';
      var gotSeason = '';
      
      return {
        saveMovies: saveMovies,
        saveMovieSelected: saveMovieSelected,
        getMovieSelected: getMovieSelected,
        saveFoods: saveFoods,
        saveFoodSelected: saveFoodSelected,
        getFoodSelected: getFoodSelected,
        saveGotSeason: saveGotSeason,
        getGotSeason: getGotSeason
      };

      function saveMovies(results) {
        movies = results;
      }

      function saveMovieSelected(id) {
        movieId = id;
      }

      function getMovieSelected() {
        var movieSelected = '';

        movies.find(function(movie){
          if (movie.id === parseInt(movieId)) {
             movieSelected = movie;
          }
        });

        return movieSelected;
      }

      function saveFoods(results) {
        foods = results;
      }

      function saveFoodSelected(id) {
        foodId = id;
      }

      function getFoodSelected() {
        var foodSelected = '';

        foods.find(function(food){
          if (food.id === parseInt(foodId)) {
            foodSelected = food;
          }
        });

        return foodSelected;
      }

      function saveGotSeason(id) {
        gotSeason = id;
      }

      function getGotSeason() {
        return gotSeason;
      }
    }
})();

